// React
import React, { useEffect, useState } from 'react'
// CSS
import '../App.css'
// DataBase
import { NewUpdatePost, UpdatePost } from '../AppData/UpdateData.js'
// Icon Library
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function Development(props) {
  // 資料處理 \n 自動換行
  const formatContent = (content) => {
    return content
      .split('\n')
      .map((line, index) => <span key={index}>{line}</span>)
  }

  // 收合指定數量外的更新日誌
  const [showAll, setShowAll] = useState(false)
  const visiblePosts = showAll ? UpdatePost : UpdatePost.slice(0, 1)

  const handleToggleShowAll = () => {
    setShowAll(!showAll)
  }

  return (
    <main
      id="development"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'settingOpen' : ''
      }`}>
      <div id="development-view">
        <div id="development-view-content">
          <h1>當前版本</h1>
          <section className="development-block">
            {NewUpdatePost.map((NewUpdatePost, index) => (
              <div className="development-post bg-purple-blue" key={index}>
                <h1>{NewUpdatePost.title}</h1>
                <h5>{NewUpdatePost.time}</h5>
                {formatContent(NewUpdatePost.content)}
              </div>
            ))}
          </section>
          <h1>歷史版本</h1>
          {UpdatePost.length > 1 && (
            <button
              className={`expand-button ${showAll ? 'open' : ''}`}
              onClick={handleToggleShowAll}>
              <FontAwesomeIcon
                icon={
                  showAll
                    ? 'fa-solid fa-circle-chevron-up'
                    : 'fa-solid fa-circle-chevron-down'
                }
              />
              {showAll ? '　收合　' : '展開全部'}
            </button>
          )}
          <section className="development-block">
            {visiblePosts.map((post, index) => (
              <div
                key={index}
                className="development-post bg-history"
                style={{ scrollSnapAlign: showAll ? 'start' : '' }}>
                <h3>{post.title}</h3>
                <h5>{post.time}</h5>
                {formatContent(post.content)}
              </div>
            ))}
            <div className="moreVersion" onClick={handleToggleShowAll}>
              {!showAll && (
                <>
                  <span>更多版本</span>
                  <FontAwesomeIcon icon="fa-solid fa-caret-down" />
                </>
              )}
              {showAll && (
                <>
                  <FontAwesomeIcon icon="fa-solid fa-sort-up" />
                  <span>收起</span>
                </>
              )}
            </div>
          </section>

          <h1>開發資訊</h1>
          <section className="development-block">
            <div className="development-post bg-em">
              <h5>User-Agent</h5>
              <span>{navigator.userAgent}</span>
            </div>
            <div className="development-post bg-em">
              <h5>Equipment Type</h5>
              <span>{props.OperationalStatus}</span>
            </div>
            <div className="development-post bg-em">
              <h5>Theme Mode</h5>
              <span>{props.themeInfo}</span>
            </div>
          </section>
          <hr style={{ color: '#000000' }} />
          <section className="development-block">
            <div>
              <div>
                <FontAwesomeIcon icon="fa-brands fa-github" />
                　敬請期待...
              </div>
            </div>
          </section>
        </div>
      </div>
    </main>
  )
}

export default Development
